import { SESSION_DATA_SUBMITTED, UPDATE_TELCO, UPDATE_TOKEN, UPDATE_SESSSION_STATUS } from '../actions/types';

const INITIAL_STATE = {
  nationalID: '',
  phoneNumber: '',
  channel: '',
  referrer: window.location.href,
  token: null,
  status: null
};

export default function(state = INITIAL_STATE, action) {
  switch (action.type) {
    case SESSION_DATA_SUBMITTED:
      return {
        ...state,
        ...action.payload
      };
    case UPDATE_TELCO:
      return {
        ...state,
        channel: action.payload
      };
    case UPDATE_TOKEN:
      return {
        ...state,
        token: action.payload
      };
    case UPDATE_SESSSION_STATUS:
      return {
        ...state,
        status: action.payload
      };
    default:
      return state;
  }
}



