import { TextField, SelectField } from '@components/Fields';
import { ChannelOptions, COUNTRY_CODE } from '@constants';

export const fields = {
  nationalID: {
    name: 'nationalID',
    type: 'string',
    label: {
      key: 'sessionScreen.nationalID'
    },
    placeholder: {
      key: 'sessionScreen.nationalID'
    },
    component: TextField,
    validation: {
      isRequired: true
    }
  },
  phoneNumber: {
    name: 'phoneNumber',
    type: 'number',
    label: {
      key: 'sessionScreen.phoneNumber'
    },
    placeholder: {
      key: 'sessionScreen.phoneNumber'
    },
    component: TextField,
    addOnText: COUNTRY_CODE,
    validation: {
      isRequired: true,
      maxLength: 20,
      minLength: 10
    }
  },
  channel: {
    name: 'channel',
    type: 'string',
    label: {
      key: 'sessionScreen.channel'
    },
    placeholder: {
      key: 'sessionScreen.channel'
    },
    component: SelectField,
    options: ChannelOptions,
    validation: {
      isRequired: true
    }
  },
  referrer: {
    name: 'referrer',
    type: 'string',
    label: {
      key: 'sessionScreen.referrer'
    },
    placeholder: {
      key: 'sessionScreen.referrer'
    },
    component: TextField,
    readOnly: true,
    hidden: true
  }
};

export const SessionFormFields = formFields => {
  let fieldsToRender = formFields.map(field => {
    return fields[field];
  });
  return fieldsToRender;
};

export const SessionFormSubmitButton = {
  label: {
    key: 'common.actions.continue'
  }
};

export const SessionFormErrors = {
  nationalID: 'errors.sessionForm.nationalIDError',
  phoneNumber: 'errors.sessionForm.phoneNumberError',
  channel: 'errors.sessionForm.channelError',
  referrer: 'errors.sessionForm.referrerError',
  genericError: 'errors.genericError'
};



