import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import { sessionActions as actions } from '@actions';
import PropTypes from 'prop-types';
import FormContainer from './FormContainer';
import Sidebar from '@components/Sidebar';
import ThankYou from '@pages/ThankYou';
import BadStatus from '@pages/BadStatus';
import RejectedApplication from '@pages/RejectedApplication';
import Unauthorized from '@pages/Unauthorized';
import { THANK_YOU_STEP, UNAUTHORIZED_STEP, BAD_STATUS_STEP, REJECTED_APPLICATION_STEP } from '@constants';
import { tokenService } from '@helpers/Token';

export class MainLayout extends React.Component {
  constructor(props) {
    super(props);
    if (tokenService.getToken()) {
      this.props.updateToken(tokenService.getToken());
    }
  }

  componentDidMount() {
    if (!tokenService.getToken()) {
      this.props.callVerifyLink();
    } else {
      this.props.getSessionStatus();
    }
  }

  renderFullBox() {
    const { currentStep } = this.props;
    return (
      <Fragment>
        <Sidebar currentStep={currentStep} />
        <FormContainer currentStep={currentStep} />
      </Fragment>
    );
  }
  renderContent() {
    const { currentStep } = this.props;
    switch (currentStep) {
      case BAD_STATUS_STEP:
        return <BadStatus />;
      case THANK_YOU_STEP:
        return <ThankYou />;
      case UNAUTHORIZED_STEP:
        return <Unauthorized />;
      case REJECTED_APPLICATION_STEP:
        return <RejectedApplication />;
      default:
        return (
          <main id='main' className='site-main'>
            <section className='register-section'>
              <div className='container'>
                <div className='step-register-fullbox'>{this.renderFullBox()}</div>
              </div>
            </section>
          </main>
        );
    }
  }
  render() {
    return this.renderContent();
  }
}

MainLayout.propTypes = {
  callVerifyLink: PropTypes.func.isRequired,
  getSessionStatus: PropTypes.func.isRequired,
  token: PropTypes.string,
  currentStep: PropTypes.number,
  updateToken: PropTypes.func.isRequired
};

const mapStateToProps = ({ currentStep }) => ({
  currentStep
});

export default connect(
  mapStateToProps,
  actions
)(MainLayout);



