import React from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import SuccessIcon from './SuccessIcon';
import ErrorIcon from './ErrorIcon';
import InfoIcon from './InfoIcon';

import styles from './styles';

export default class Alert extends React.Component {
  render() {
    const { message, type, className } = this.props;
    const classes = cx(`message-custom-content message-${type}`, className);
    const iconStyles = Object.assign({}, styles.icon, styles[`${type}IconStyle`]);
    const iconType =
      type === 'success' ? (
        <SuccessIcon iconStyles={iconStyles} iconSvgStyle={styles.iconSvg} />
      ) : type === 'error' ? (
        <ErrorIcon iconStyles={iconStyles} iconSvgStyle={styles.iconSvg} />
      ) : (
        <InfoIcon iconStyles={iconStyles} iconSvgStyle={styles.iconSvg} />
      );
    return (
      <div className='message' style={styles.message}>
        <div className='message-notice' style={styles.messageNotice}>
          <div className='message-notice-content' style={styles.messageNoticeContent}>
            <div className={classes}>
              {iconType}
              <span>{message}</span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Alert.propTypes = {
  message: PropTypes.string.isRequired,
  type: PropTypes.string,
  className: PropTypes.string
};

Alert.defaultProps = {
  type: 'info',
  className: ''
};



