import React from 'react';
import PropTypes from 'prop-types';
import { CREATE_SESSION_STEP, OTP_VERIFY_STEP } from '@constants';
import Session from '@pages/Session';
import OtpVerification from '@pages/OtpVerification';

class FormContainer extends React.Component {
  componentDidUpdate(prevProps) {
    if (prevProps.currentStep !== this.props.currentStep) {
      this.scrollBackTop();
    }
  }

  scrollBackTop() {
    window.scrollTo(0, 0);
  }

  renderForm() {
    switch (this.props.currentStep) {
      case CREATE_SESSION_STEP:
        return <Session />;
      case OTP_VERIFY_STEP:
        return <OtpVerification />;
      default:
        return <Session />;
    }
  }

  render() {
    return (
      <div className={`right-regsiter step${this.props.currentStep}`}>
        <div className='register-step show'>{this.renderForm()}</div>
      </div>
    );
  }
}

FormContainer.propTypes = {
  currentStep: PropTypes.number
};

export default FormContainer;



