import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';
import { Formik } from 'formik';
import { DynamicFormField } from '@components/Fields';
import Anwsers from '@helpers/decorators/Anwsers';
import Button from '@components/Button';
import ValidationQuestions from '@helpers/ValidateQuestions';

class AdditionalInformationForm extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { nextQuestions, submitNextQuestions, t } = this.props;
    const initialValues = new Anwsers(nextQuestions);
    const validations = new ValidationQuestions(nextQuestions, t);

    return (
      <Formik initialValues={initialValues} onSubmit={submitNextQuestions} validationSchema={validations.schema}>
        {({ handleSubmit, handleChange, isSubmitting, isValid }) => (
          <form onSubmit={handleSubmit} className='regsiter-form'>
            <fieldset>
              {nextQuestions.map(question => {
                return <DynamicFormField key={question.id} {...question} onChange={handleChange} />;
              })}
            </fieldset>
            <div className='regsiter-form-action clearfix'>
              <Button type='submit' outline={false} disabled={isSubmitting || !isValid}>
                {t('additional_information.submit')}
              </Button>
            </div>
          </form>
        )}
      </Formik>
    );
  }
}

AdditionalInformationForm.propTypes = {
  nextQuestions: PropTypes.array,
  submitNextQuestions: PropTypes.func.isRequired,
  t: PropTypes.func.isRequired
};

AdditionalInformationForm.defaultProps = {
  nextQuestions: []
};

export default translate('dj-webhandler')(AdditionalInformationForm);



