import React from 'react';
import { translate } from 'react-i18next';
import PropTypes from 'prop-types';
import SadSvg from '@src/assets/images/sad.svg';
import Step1Svg from '@src/assets/images/messages.svg';
import Step2Svg from '@src/assets/images/find.svg';
import Step3Svg from '@src/assets/images/Click.svg';

import '../ThankYou/ThankYou.css';

const Unauthorized = props => {
  const { t } = props;
  const translate = t;
  return (
    <div className='container'>
      <div className='step-register-qualified-fullbox'>
        <div className='qualified-container'>
          <img src={SadSvg} />
          <div className='register-qualified-title'>{translate('unauthorizedPage.title')}</div>
          <div className='register-qualified-meta'>{translate('unauthorizedPage.subTitle')}</div>
          <div className='steps-of-instruction'>
            <div className='step step-1'>
              <div className='step-image'>
                <img src={Step1Svg} />
                <div className='step-dots' />
              </div>
              <div className='step-message'>{translate('unauthorizedPage.step1Message')}</div>
            </div>
            <div className='step step-2'>
              <div className='step-image'>
                <img src={Step2Svg} />
                <div className='step-dots' />
              </div>
              <div className='step-message'>{translate('unauthorizedPage.step2Message')}</div>
            </div>
            <div className='step step-3'>
              <div className='step-image'>
                <img src={Step3Svg} />
              </div>
              <div className='step-message'>{translate('unauthorizedPage.step3Message')}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

Unauthorized.propTypes = {
  t: PropTypes.func.isRequired
};

const EnhancedUnauthorizedPage = translate('dj-webhandler')(Unauthorized);
EnhancedUnauthorizedPage.displayName = 'Unauthorized';

export default EnhancedUnauthorizedPage;



