import isArray from 'lodash/isArray';
import isFunction from 'lodash/isFunction';
import isNil from 'lodash/isNil';
import keys from 'lodash/keys';

import VALIDATION_TYPES from '@constants/validations';
import QUESTION_TYPES from '@constants/questions';
import moment from 'moment';
import * as yup from 'yup';

/** Validation schema - based on yup lib. */
class ValidationQuestions {
  /**
   * Create a validation schema
   * @param {Array} data - A list of questions which is returned from getNextQuestions api
   * @param {Function} translate - Translate function
   */
  constructor(data, translate) {
    if (!isArray(data) || !isFunction(translate)) return {};

    const validationSchema = {};
    this.translate = translate;
    data.forEach(item => {
      validationSchema[item.id] = this._convertToValidationYup(item);
    });

    // Yup schema
    this.schema = yup.object().shape(validationSchema);
  }

  /**
   * Generate a yup object
   * @param {Object} item - A question object
   * @return {Object} yupObj - A yup instance
   */
  _convertToValidationYup = item => {
    let yupObj;
    // Checking its type
    yupObj = this._checkType(item);
    // Checking mandatory rule
    yupObj = this._checkMandatory(item, yupObj);
    // Checking its validation rules
    yupObj = this._checkValidation(item, yupObj);

    return yupObj;
  };

  _checkType = (item, yupObj = {}) => {
    if (!item || isNil(item.type)) return null;
    const errorMessage = this.translate('validations.wrongFormat');

    switch (item.type) {
      case QUESTION_TYPES.TEXT: {
        yupObj = yup.string(errorMessage);
        break;
      }
      case QUESTION_TYPES.DATE: {
        yupObj = yup.date(errorMessage);
        break;
      }
      default:
        yupObj = yup.string(errorMessage);
    }

    return yupObj;
  };

  _checkValidation = (item, validation) => {
    const validationRules = item.validations;
    if (isNil(validationRules)) return validation;

    keys(validationRules).forEach(ruleName => {
      const validationValue = validationRules[ruleName];
      switch (ruleName) {
        case VALIDATION_TYPES.MAX_LENGTH: {
          const errorMessage = this.translate('validations.max', { validationValue });
          validation = validation.max(validationValue, errorMessage);
          break;
        }
        case VALIDATION_TYPES.DATE_BEFORE: {
          const m = moment(validationValue);
          const errorMessage = this.translate('validations.before', { validationValue: m.format('LL') });
          if (m.isValid()) {
            validation = validation.max(m.toDate(), errorMessage);
          }
          break;
        }
        case VALIDATION_TYPES.MIN_LENGTH: {
          const errorMessage = this.translate('validations.min', { validationValue });
          validation = validation.min(validationValue, errorMessage);
          break;
        }
        case VALIDATION_TYPES.DATE_AFTER: {
          const m = moment(validationValue);
          const errorMessage = this.translate('validations.after', { validationValue: m.format('LL') });
          if (m.isValid()) {
            validation = validation.min(m.toDate(), errorMessage);
          }
          break;
        }
        case VALIDATION_TYPES.REGEX: {
          validation = validation.matches(validationValue);
          break;
        }
        default: {
          break;
        }
      }
    });

    return validation;
  };

  _checkMandatory = (item, validation) => {
    const { isRequired } = item;
    if (isRequired) {
      validation = validation.required(this.translate('validations.isRequired'));
    }

    return validation;
  };
}

export default ValidationQuestions;



