import React from 'react';
import PropTypes from 'prop-types';
import { OtpField } from '@components/Fields';
import { util } from '@helpers/utils';
import { BACKSPACE, LEFT_ARROW, RIGHT_ARROW, DELETE } from '@constants/keyCode';

class OtpInput extends React.Component {
  state = {
    activeInput: 0,
    otp: []
  };

  componentDidMount() {
    const { otp } = this.props;
    const otpArray = otp.split('');
    if (otpArray.length) {
      this.setState({
        otp: otpArray
      });
    }
  }

  componentWillReceiveProps(nextProps) {
    const { otp } = nextProps;
    const currentStateOtpString = this.state.otp.join('');
    if (currentStateOtpString !== otp) {
      const otpArray = otp.split('');
      this.setState({
        otp: otpArray
      });
    }
  }

  // Helper to return OTP from input
  getOtp = () => {
    this.props.onChange(this.state.otp.join(''));
  };

  // Focus on input by index
  focusInput = input => {
    const { numInputs } = this.props;
    const activeInput = Math.max(Math.min(numInputs - 1, input), 0);

    this.setState({
      activeInput
    });
  };

  // Focus on next input
  focusNextInput = () => {
    const { activeInput } = this.state;
    this.focusInput(activeInput + 1);
  };

  // Focus on previous input
  focusPrevInput = () => {
    const { activeInput } = this.state;
    this.focusInput(activeInput - 1);
  };

  // Change OTP value at focused input
  changeCodeAtFocus = value => {
    const { activeInput, otp } = this.state;
    otp[activeInput] = value;

    this.setState({
      otp
    });
    this.getOtp();
  };

  // Handle pasted OTP
  handleOnPaste = e => {
    e.preventDefault();
    const { numInputs } = this.props;
    const { activeInput, otp } = this.state;

    // Get pastedData in an array of max size (num of inputs - current position)
    const pastedData = e.clipboardData
      .getData('text/plain')
      .slice(0, numInputs - activeInput)
      .split('');

    // Paste data from focused input onwards
    for (let pos = 0; pos < numInputs; ++pos) {
      if (pos >= activeInput && pastedData.length > 0) {
        otp[pos] = pastedData.shift();
      }
    }

    this.setState({
      otp
    });

    this.getOtp();
  };

  handleOnChange = e => {
    this.changeCodeAtFocus(e.target.value);
    this.focusNextInput();
  };

  // Handle cases of backspace, delete, left arrow, right arrow
  handleOnKeyDown = e => {
    switch (e.keyCode) {
      case BACKSPACE:
        e.preventDefault();
        this.changeCodeAtFocus('');
        this.focusPrevInput();
        break;
      case DELETE:
        e.preventDefault();
        this.changeCodeAtFocus('');
        break;
      case LEFT_ARROW:
        e.preventDefault();
        this.focusPrevInput();
        break;
      case RIGHT_ARROW:
        e.preventDefault();
        this.focusNextInput();
        break;
      default:
        break;
    }
  };

  renderInputs = () => {
    const { activeInput, otp } = this.state;
    const {
      numInputs,
      inputStyle,
      focusStyle,
      separator,
      isDisabled,
      disabledStyle,
      hasErrored,
      errorStyle,
      shouldAutoFocus
    } = this.props;
    const inputs = [];

    for (let i = 0; i < numInputs; i++) {
      inputs.push(
        <OtpField
          key={i}
          focus={activeInput === i}
          value={(otp && otp[i]) || ''}
          onChange={this.handleOnChange}
          onKeyDown={this.handleOnKeyDown}
          onPaste={this.handleOnPaste}
          onFocus={e => {
            this.setState({
              activeInput: i
            });
            e.target.select();
          }}
          onBlur={() => this.setState({ activeInput: -1 })}
          separator={separator}
          inputStyle={inputStyle}
          focusStyle={focusStyle}
          isLastChild={i === numInputs - 1}
          isDisabled={isDisabled}
          disabledStyle={disabledStyle}
          hasErrored={hasErrored}
          errorStyle={errorStyle}
          shouldAutoFocus={shouldAutoFocus}
        />
      );
    }

    return inputs;
  };

  render() {
    const { containerStyle } = this.props;

    return (
      <div
        style={Object.assign({ display: 'flex' }, util.isStyleObject(containerStyle) && containerStyle)}
        className={!util.isStyleObject(containerStyle) && containerStyle}
      >
        {this.renderInputs()}
      </div>
    );
  }
}

OtpInput.propTypes = {
  otp: PropTypes.string,
  numInputs: PropTypes.number,
  onChange: PropTypes.func.isRequired,
  separator: PropTypes.object,
  containerStyle: PropTypes.object,
  inputStyle: PropTypes.object,
  focusStyle: PropTypes.object,
  isDisabled: PropTypes.bool,
  disabledStyle: PropTypes.object,
  hasErrored: PropTypes.bool,
  errorStyle: PropTypes.object,
  shouldAutoFocus: PropTypes.bool,
  t: PropTypes.func
};

OtpInput.defaultProps = {
  otp: '',
  numInputs: 6,
  onChange: otp => console.log(otp),
  isDisabled: false,
  shouldAutoFocus: false
};

export default OtpInput;



