import cx from 'classnames';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { QUESTION_TYPES } from '@src/constants/questions';
import { TextField, SelectField, DatePickerField } from '@components/Fields';
import { Field } from 'formik';

export class DynamicFormField extends Component {
  renderField = field => {
    if (!field) return null;
    const fieldProps = {
      key: field.id,
      id: field.id,
      name: field.id,
      type: field.type,
      label: field.question,
      placeholder: field.placeholder,
      onChange: field.onChange
    };

    switch (field.type) {
      case QUESTION_TYPES.SELECT:
        return <Field component={SelectField} {...fieldProps} options={field.options} />;
      case QUESTION_TYPES.DATE:
        return <Field component={DatePickerField} {...fieldProps} />;
      default:
        return <Field component={TextField} {...fieldProps} />;
    }
  };

  render() {
    const { viewOnly, ...field } = this.props;
    return (
      <div className={cx({ 'input-info': viewOnly })}>
        {viewOnly ? <span className='view-only-field'>{field.value}</span> : this.renderField(field)}
      </div>
    );
  }
}

DynamicFormField.propTypes = {
  field: PropTypes.object,
  viewOnly: PropTypes.bool
};

DynamicFormField.defaultProps = {
  field: {},
  viewOnly: false
};

export default DynamicFormField;



