import {
  GET_NEXT_QUESTIONS_SUCCESS,
  GET_NEXT_QUESTIONS_FAIL,
  UPDATE_STEP,
  CLEAR_ADDITIONAL_INFO_STATE,
  SUBMITTING_ANSWERS,
  SUBMIT_ANSWERS_SUCCESS,
  SUBMIT_ANSWERS_FAIL
} from './types';
import { getNextQuestionsApi, submitAnswersApi } from '@apis/api';
import { SUMMARY_STEP } from '@constants';
import { get } from 'lodash';

export function getNextQuestions() {
  return dispatch => {
    return getNextQuestionsApi()
      .then(response => {
        const questions = get(response, 'data.data.fields', []);
        // Update the new questions
        if (questions && questions.length > 0) {
          dispatch({
            type: GET_NEXT_QUESTIONS_SUCCESS,
            payload: questions
          });
        }
        // Return a empty questions, so moving to the summary page
        else {
          // Clean up additional info stage
          dispatch({
            type: CLEAR_ADDITIONAL_INFO_STATE
          });
          // Update to the next step
          dispatch({
            type: UPDATE_STEP,
            payload: SUMMARY_STEP
          });
        }
      })
      .catch(error => {
        dispatch({
          type: GET_NEXT_QUESTIONS_FAIL,
          payload: error
        });
      });
  };
}

export function submittingAnwser() {
  return {
    type: SUBMITTING_ANSWERS
  };
}

export function submitAnswers(data) {
  return dispatch => {
    // Dispatch a signal to redux to turn on isSubmitting flag
    dispatch(submittingAnwser());
    // Submit the answers
    return submitAnswersApi(data)
      .then(() => {
        dispatch({
          type: SUBMIT_ANSWERS_SUCCESS
        });
        dispatch(getNextQuestions());
      })
      .catch(error => {
        dispatch({
          type: SUBMIT_ANSWERS_FAIL,
          payload: error
        });
      });
  };
}



