const styles = {
  message: {
    fontsize: '14px',
    fontVariant: 'tabular-nums',
    lineHeight: '1.5',
    color: 'rgba(0, 0, 0, 0.65)',
    boxSizing: 'border-box',
    margin: '0',
    padding: '0',
    listStyle: 'none',
    position: 'fixed',
    zIndex: '1010',
    width: '100%',
    top: '16px',
    left: '0',
    pointerEvents: 'none'
  },
  messageNotice: {
    padding: '8px',
    textAlign: 'center'
  },
  messageNoticeContent: {
    padding: '10px 16px',
    borderRadius: '4px',
    boxShadow: '0 4px 12px rgba(0, 0, 0, 0.15)',
    background: '#fff',
    display: 'inline-block',
    pointerEvents: 'all'
  },
  icon: {
    marginRight: '8px',
    fontSize: '16px',
    top: '1px',
    position: 'relative',
    display: 'inline-block',
    fontStyle: 'normal',
    verticalAlign: '-0.125em',
    textAlign: 'center',
    textTransform: 'none',
    lineHeight: '1',
    textRendering: 'optimizeLegibility'
  },
  successIconStyle: {
    color: '#52c41a'
  },
  errorIconStyle: {
    color: '#f5222d'
  },
  infoIconStyle: {
    color: '#faad14'
  },
  iconSvg: {
    display: 'block',
    overflow: 'hidden'
  }
};

export default styles;



