import { HTTPMethod } from './http';
import { api } from '@helpers/ApiLibrary';
import * as urls from './endpoints';

export const verifyLinkApi = data => {
  const request = {
    method: HTTPMethod.POST,
    url: urls.VERIFY_LINK_ENDPOINT,
    data: data
  };
  return api.request(request);
};

export const createSessionApi = data => {
  const request = {
    method: HTTPMethod.POST,
    url: urls.CREATE_SESSION_ENDPOINT,
    data: data
  };
  return api.request(request);
};

export const getSessionStatusApi = () => {
  const request = {
    method: HTTPMethod.GET,
    url: urls.GET_SESSION_STATUS_ENDPOINT
  };
  return api.request(request);
};

export const verifyOtpApi = data => {
  const request = {
    method: HTTPMethod.POST,
    url: urls.VERIFY_OTP_ENDPOINT,
    data: data
  };
  return api.request(request);
};

export const resendOtpApi = () => {
  const request = {
    method: HTTPMethod.POST,
    url: urls.RESEND_OTP_ENDPOINT
  };
  return api.request(request);
};

export const getNextQuestionsApi = () => {
  const request = {
    method: HTTPMethod.GET,
    url: urls.GET_NEXT_QUESTIONS_ENDPOINT
  };
  return api.request(request);
};

export const submitAnswersApi = data => {
  const formattedData = Object.keys(data).map(questionId => {
    const questionAndAnswer = {};
    questionAndAnswer['id'] = questionId;
    questionAndAnswer['answer'] = data[questionId];
    return questionAndAnswer;
  });
  const request = {
    method: HTTPMethod.POST,
    url: urls.SUBMIT_ANSWERS_ENDPOINT,
    data: {
      data: formattedData
    }
  };
  return api.request(request);
};

export const getApplicationDataApi = () => {
  const request = {
    method: HTTPMethod.GET,
    url: urls.GET_APPLICATION_DATA
  };
  return api.request(request);
};

export const submitApplicationDataApi = () => {
  const request = {
    method: HTTPMethod.POST,
    url: urls.POST_APPLICATION_DATA
  };
  return api.request(request);
};



