import React from 'react';
import PropTypes from 'prop-types';
import { SideBar as SideBarConstants, IS_TELCO } from '@constants';
import { translate } from 'react-i18next';
import StepItem from './StepItem';

const Sidebar = ({ currentStep, t }) => {
  const translate = t;
  let steps = SideBarConstants.steps;
  if (IS_TELCO) {
    steps = steps.filter(step => step.showTelco);
  }
  return (
    <div className={`left-register step${currentStep}`}>
      <ul>
        {steps.map((step, idx) => (
          <StepItem
            key={idx}
            icon={step.icon}
            label={translate(step.key)}
            complete={idx <= currentStep}
            active={idx == currentStep}
          />
        ))}
      </ul>
    </div>
  );
};

Sidebar.displayName = 'Sidebar';

Sidebar.propTypes = {
  currentStep: PropTypes.number,
  t: PropTypes.func
};

Sidebar.defaultProps = {
  currentStep: 0
};

export default translate('dj-webhandler')(Sidebar);



