import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import * as actions from '@actions';
import { OtpForm } from '@components/Forms';

export class OtpVerification extends React.Component {
  render() {
    const { updateToken, updateStep, logOut } = this.props;
    return (
      <div className='form-wrapper'>
        <OtpForm updateToken={updateToken} updateStep={updateStep} logOut={logOut} />
      </div>
    );
  }
}

OtpVerification.propTypes = {
  t: PropTypes.func,
  updateToken: PropTypes.func.isRequired,
  updateStep: PropTypes.func.isRequired,
  logOut: PropTypes.func.isRequired
};

const connectedComponent = connect(
  null,
  actions
)(OtpVerification);

connectedComponent.displayName = 'OtpVerification';

export default connectedComponent;



