import {
  UPDATE_PAGE_ID,
  UPDATE_TELCO,
  SESSION_DATA_SUBMITTED,
  UPDATE_SESSSION_STATUS,
  UPDATE_STEP,
  UPDATE_TOKEN
} from './types';
import { verifyLinkApi, getSessionStatusApi } from '@apis/api';
import { api } from '@helpers/ApiLibrary';
import { tokenService } from '@helpers/Token';
import { STATUS_TO_STEP_MAPPING, CREATE_SESSION_STEP, UNAUTHORIZED_STEP, BAD_STATUS_STEP } from '@constants';
import { HTTP_CODE_UNAUTHORIZED } from '@apis/http';

export function callVerifyLink() {
  return dispatch => {
    return verifyLinkApi({
      referrer: window.location.href
    })
      .then(response => {
        const { data } = response;
        if (data) {
          const responseData = data.data;
          if (responseData) {
            const { telco, pageID } = responseData;
            if (telco) {
              dispatch(updateTelco(telco));
            }
            dispatch(updatePageId(pageID));
          }
        }
      })
      .catch(() => {
        dispatch(updateStep(UNAUTHORIZED_STEP));
      });
  };
}
export function getSessionStatus() {
  return dispatch => {
    return getSessionStatusApi()
      .then(response => {
        const { data } = response;
        if (data) {
          const { status } = data.data;
          dispatch({
            type: UPDATE_SESSSION_STATUS,
            payload: status
          });
          let currentStep = STATUS_TO_STEP_MAPPING[status];
          if (!currentStep) currentStep = BAD_STATUS_STEP;
          dispatch(updateStep(currentStep));
        }
      })
      .catch(error => {
        if (error && error.response && error.response.status === HTTP_CODE_UNAUTHORIZED) {
          dispatch(logOut());
        }
      });
  };
}
export function createSessionAndUpdateToken(session, token) {
  return dispatch => {
    dispatch(createSession(session));
    dispatch(updateToken(token));
  };
}

export function createSession(session) {
  return {
    type: SESSION_DATA_SUBMITTED,
    payload: session
  };
}

export function updateToken(token) {
  api.setAuthorizationToken(token);
  if (token) {
    tokenService.setToken(token);
  } else {
    tokenService.removeToken();
  }
  return {
    type: UPDATE_TOKEN,
    payload: token
  };
}

export function updateTelco(telco) {
  return {
    type: UPDATE_TELCO,
    payload: telco
  };
}

export function updatePageId(pageId) {
  return {
    type: UPDATE_PAGE_ID,
    payload: pageId
  };
}

export function updateStep(currentStep) {
  return {
    type: UPDATE_STEP,
    payload: currentStep
  };
}

export function logOut() {
  return dispatch => {
    dispatch(updateToken(null));
    dispatch(updateStep(CREATE_SESSION_STEP));
    dispatch(callVerifyLink());
  };
}



