import React from 'react';
import { connect } from 'react-redux';
import { translate } from 'react-i18next';
import PropTypes from 'prop-types';
import * as actions from '@actions';
import { PAGE_IDS } from '@constants';
import { SessionForm } from '@components/Forms';
import Spinner from '@components/Spinner';

export class Session extends React.Component {
  renderSessionForm() {
    const { pageId, session } = this.props;
    const { nationalID, phoneNumber, channel, referrer } = session;

    let fields = null;
    if (pageId) {
      if (pageId === PAGE_IDS.PNT) {
        fields = {
          nationalID,
          phoneNumber,
          channel,
          referrer
        };
      } else {
        fields = {
          nationalID,
          channel,
          referrer
        };
      }
      return (
        <SessionForm
          fields={fields}
          createSessionAndUpdateToken={this.props.createSessionAndUpdateToken}
          updateStep={this.props.updateStep}
        />
      );
    } else {
      return <Spinner />;
    }
  }
  render() {
    const { t } = this.props;
    const translate = t;
    return (
      <div className='form-wrapper'>
        <h2 className='register-form-title'>{translate('registration.title')}</h2>
        {this.renderSessionForm()}
      </div>
    );
  }
}

Session.propTypes = {
  t: PropTypes.func,
  pageId: PropTypes.string,
  updateStep: PropTypes.func,
  createSessionAndUpdateToken: PropTypes.func,
  session: PropTypes.object.isRequired
};

const mapStateToProps = ({ pageId, session }) => ({
  pageId,
  session
});

const connectedComponent = connect(
  mapStateToProps,
  actions
)(translate('dj-webhandler')(Session));

connectedComponent.displayName = 'Session';

export default connectedComponent;



