import React from 'react';
import { translate } from 'react-i18next';
import PropTypes from 'prop-types';
import SadSvg from '@src/assets/images/sad.svg';

// Reuse the thankyou.css for now.
import '../ThankYou/ThankYou.css';

const RejectedApplication = props => {
  const { t } = props;
  const translate = t;
  return (
    <div className='container'>
      <div className='step-register-qualified-fullbox'>
        <div className='qualified-container'>
          <img src={SadSvg} />
          <div className='register-qualified-title'>{translate('rejectedApplicationPage.title')}</div>
          <div className='register-qualified-meta'>{translate('rejectedApplicationPage.subTitle')}</div>
        </div>
      </div>
    </div>
  );
};

RejectedApplication.propTypes = {
  t: PropTypes.func.isRequired
};

const EnhancedRejectedApplication = translate('dj-webhandler')(RejectedApplication);
EnhancedRejectedApplication.displayName = 'RejectedApplication';

export default EnhancedRejectedApplication;



