import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import Button from '@components/Button';
import Alert from '@components/Alert';
import Spinner from '@components/Spinner';
import OtpInput from '@components/OtpInput';
import { translate } from 'react-i18next';
import { util } from '@helpers/utils';
import { THANK_YOU_STEP, NOTIFICATION_TIMEOUT } from '@constants';
import { OTP_VERDICTS, OTP_LENGTH } from '@constants/otp';
import { verifyOtpApi, resendOtpApi } from '@apis/api';
import { HTTP_CODE_UNAUTHORIZED, HTTP_CODE_NOT_FOUND, HTTP_CODE_AUTHENTICATION_TIMEOUT } from '@apis/http';

const styles = {
  inputStyle: {
    width: '40px',
    textAlign: 'center',
    height: '60px',
    border: '1px solid #e4e4e4',
    backgroundColor: '#fff',
    borderRadius: '5px',
    marginTop: '10px',
    padding: '10px',
    marginRight: '5px',
    marginLeft: '5px',
    fontWeight: 'bold',
    fontSize: '18px'
  },
  errorStyle: {
    border: '1px solid red'
  },
  disabledStyle: {
    border: '1px solid #e4e4e4',
    cursor: 'not-allowed'
  }
};

class OtpForm extends React.Component {
  state = {
    otp: '',
    separator: ' ',
    isDisabled: false,
    hasErrored: false,
    retryLeft: 0,
    retryTime: null,
    message: '',
    loading: false,
    shouldRedirect: false,
    countdownSeconds: NOTIFICATION_TIMEOUT
  };

  handleOtpChange = otp => {
    this.setState({ otp });
  };

  toggleLoading = loadingState => {
    this.setState({
      loading: loadingState
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    e.stopPropagation();
    const data = {
      otp: this.state.otp
    };
    this.toggleLoading(true);
    verifyOtpApi(data)
      .then(this.handleOtpVerified)
      .catch(this.handleOtpError);
  };

  handleOtpVerified = response => {
    const { updateToken, updateStep } = this.props;
    this.toggleLoading(false);
    const { data } = response;
    if (data) {
      const responseData = data.data;
      if (responseData) {
        const { token } = responseData;
        updateToken(token);
        updateStep(THANK_YOU_STEP);
      }
    }
  };

  handleOtpError = error => {
    let errorMessage = null;
    const { t } = this.props;
    let { isDisabled, shouldRedirect, countdownSeconds } = this.state;
    const translate = t;
    if (error && error.response && error.response.data) {
      switch (error.response.data.verdict) {
        case OTP_VERDICTS.OTP_INCORRECT.verdict:
          errorMessage = translate(OTP_VERDICTS.OTP_INCORRECT.errorMessage);
          break;
        case OTP_VERDICTS.OTP_TOO_MANY_FAILED_ATTEMPTS.verdict:
          errorMessage = translate(OTP_VERDICTS.OTP_TOO_MANY_FAILED_ATTEMPTS.errorMessage);
          isDisabled = true;
          break;
        default:
          errorMessage = translate(OTP_VERDICTS.OTP_REDIRECT_ERRORS.errorMessage, {
            countdownSeconds: countdownSeconds
          });
          shouldRedirect = true;
          this.showMessageAndRedirect();
          break;
      }
    }
    this.setState({
      message: errorMessage,
      shouldRedirect,
      hasErrored: true,
      isDisabled: isDisabled,
      loading: false
    });
  };

  showMessageAndRedirect = () => {
    const { logOut, t } = this.props;
    const translate = t;
    const { countdownSeconds } = this.state;

    if (countdownSeconds < 0) {
      // Change your redirection link here
      logOut();
    } else {
      this.setState({
        message: translate(OTP_VERDICTS.OTP_REDIRECT_ERRORS.errorMessage, { countdownSeconds: countdownSeconds }),
        countdownSeconds: countdownSeconds - 1
      });
      setTimeout(this.showMessageAndRedirect, 1000);
    }
  };

  handleResend(e) {
    e.preventDefault();
    e.stopPropagation();
    this.toggleLoading(true);
    resendOtpApi()
      .then(this.handleResendSuccess)
      .catch(this.handleResendError);
  }

  handleResendSuccess = response => {
    this.toggleLoading(false);
    const { data } = response;
    if (data) {
      const responseData = data.data;
      if (responseData) {
        const { retryLeft } = responseData;
        this.handleOtpResendSuccess(retryLeft);
      }
    }
  };

  handleOtpResendSuccess = retryLeft => {
    const { t } = this.props;
    const translate = t;
    this.setState({
      hasErrored: false,
      isDisabled: false,
      message: translate('otp.otp_resent', { retryLeft: retryLeft }),
      retryLeft
    });
  };

  handleResendError = error => {
    this.toggleLoading(false);
    const { logOut, t } = this.props;
    const translate = t;
    if (error && error.response) {
      switch (error.response.status) {
        case HTTP_CODE_UNAUTHORIZED:
        case HTTP_CODE_NOT_FOUND:
          logOut();
          break;
        case HTTP_CODE_AUTHENTICATION_TIMEOUT: {
          let retryTime = util.parseHeader(error.response, 'retry-after');
          retryTime = util.millisToSeconds(retryTime);
          const errorMessage = translate('otp.retries_exhausted', { retryTime: retryTime });
          this.setState({
            retryTime,
            isDisabled: true,
            hasErrored: true,
            message: errorMessage
          });
          break;
        }
      }
    }
  };

  render() {
    const { t } = this.props;
    const { otp, separator, isDisabled, hasErrored, message, loading, retryTime, shouldRedirect } = this.state;
    const translate = t;
    return (
      <form autoComplete='off' onSubmit={this.handleSubmit} className={'register-form flip-item'}>
        <div className='instruction'>{translate('otp.instruction')}</div>
        {loading ? (
          <Spinner />
        ) : (
          <Fragment>
            {shouldRedirect ? <Alert type={'error'} message={message} /> : null}
            <fieldset>
              <div className='input-field otp-field'>
                <div className='otp-wrapper'>
                  <OtpInput
                    otp={otp}
                    numInputs={OTP_LENGTH}
                    isDisabled={isDisabled}
                    hasErrored={hasErrored}
                    errorStyle={styles.errorStyle}
                    disabledStyle={styles.disabledStyle}
                    inputStyle={styles.inputStyle}
                    onChange={this.handleOtpChange}
                    separator={<span>{separator}</span>}
                    shouldAutoFocus
                    handleSubmit={this.handleSubmit}
                  />
                </div>
                {hasErrored ? <p className='error-message'>{message}</p> : <p className='success-message'>{message}</p>}
                <div className='input-submit'>
                  <Button disabled={otp.length < OTP_LENGTH || isDisabled} type='submit' onClick={this.handleSubmit}>
                    {translate('common.actions.verify')}
                  </Button>
                </div>
              </div>
            </fieldset>
            {!retryTime && (
              <div className='instruction'>
                {translate('otp.resend_label')}
                &nbsp;
                <a
                  className='resend-code'
                  onClick={e => {
                    this.handleResend(e);
                  }}
                >
                  {translate('otp.resend_now')}
                </a>
              </div>
            )}
          </Fragment>
        )}
      </form>
    );
  }
}

OtpForm.propTypes = {
  t: PropTypes.func,
  updateToken: PropTypes.func.isRequired,
  updateStep: PropTypes.func.isRequired,
  logOut: PropTypes.func.isRequired
};

const LocalizedOtpForm = translate('dj-webhandler')(OtpForm);
LocalizedOtpForm.displayName = 'OtpForm';
export default LocalizedOtpForm;



