import React from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import isObject from 'lodash/isObject';

const SelectField = props => {
  const {
    field: { name, value, ...field },
    form: { touched, errors },
    label,
    validation,
    placeholder,
    className,
    options,
    hidden,
    ...rest
  } = props;
  const error = errors[name];
  const touch = touched[name];
  const classes = cx(
    'input-field select-field',
    {
      'error-field': !!(touch && error)
    },
    {
      'hidden-field': !!hidden
    },
    className
  );
  return (
    <div className={classes}>
      <label>
        {validation && validation.isRequired ? <span className='required'>*&nbsp;</span> : null}
        <span className='label'>{label}</span>
      </label>
      <div className='selectbox'>
        <select value={value} {...field} {...rest} id={name}>
          {value && value.trim().length ? null : <option value={''}>{placeholder}</option>}
          {options.map(
            optionValue =>
              isObject(optionValue) ? (
                <option key={optionValue.value} value={optionValue.value}>
                  {optionValue.label}
                </option>
              ) : (
                <option key={optionValue} value={optionValue}>
                  {optionValue}
                </option>
              )
          )}
        </select>
      </div>
      {touch && error && <span className='error'>{error}</span>}
    </div>
  );
};

SelectField.propTypes = {
  field: PropTypes.object,
  form: PropTypes.shape({
    errors: PropTypes.object,
    touched: PropTypes.object
  }),
  label: PropTypes.string,
  className: PropTypes.string,
  placeholder: PropTypes.string,
  options: PropTypes.array.isRequired,
  validation: PropTypes.object,
  hidden: PropTypes.bool,
  rest: PropTypes.object
};

SelectField.defaultProps = {
  field: {},
  form: {
    errors: {},
    touched: {}
  },
  name: '',
  label: '',
  className: '',
  placeholder: ''
};

export default SelectField;



