import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import Button from '@components/Button';
import { translate } from 'react-i18next';
import { SessionFormFields, SessionFormSubmitButton, SessionFormErrors } from './FormFields';
import { withFormik, Field } from 'formik';
import * as Yup from 'yup';
import { OTP_VERIFY_STEP } from '@constants';
import { createSessionApi } from '@apis/api';

let validations = null;

const setUpValidations = props => {
  const translate = props.t;
  const sessionFormFields = SessionFormFields(Object.keys(props.fields));
  validations = sessionFormFields.reduce((acc, field) => {
    acc[field.name] = Yup[field.type]();
    if (field.validation) {
      if (field.validation.isRequired) {
        acc[field.name] = acc[field.name].required(translate(SessionFormErrors[field.name]));
      }
      if (field.validation.maxLength) {
        const validationObject = {
          validationValue: field.validation.maxLength
        };
        acc[field.name] = acc[field.name].test(
          field.name,
          `${translate('validations.max', validationObject)}`,
          val => val && val.toString().length <= field.validation.maxLength
        );
      }
    }
    return acc;
  }, {});
};

export const validationSchema = () => {
  return Yup.object().shape(validations);
};

export const mapPropsToValues = ({ fields }) => ({
  ...fields
});

export const handleSubmit = (payload, actions) => {
  const { nationalID, phoneNumber, channel, referrer } = payload;
  const { updateStep, createSessionAndUpdateToken } = actions.props;
  const formData = {
    nationalID: nationalID,
    phoneNumber: phoneNumber.toString(),
    channel: channel,
    referrer: referrer
  };
  createSessionApi(formData)
    .then(response => {
      const { data } = response;
      actions.setStatus({
        backendError: false
      });
      if (data) {
        const responseData = data.data;
        if (responseData) {
          const { token } = responseData;
          createSessionAndUpdateToken(formData, token);
        }
        updateStep(OTP_VERIFY_STEP);
      }
    })
    .catch(() => {
      actions.setStatus({
        backendError: true
      });
    });
};

export class SessionForm extends React.Component {
  constructor(props) {
    super(props);
    props.setStatus({
      backendError: false
    });
    setUpValidations(props);
  }
  render() {
    const { fields, values, handleSubmit, status, t } = this.props;
    const translate = t;
    const sessionFormFields = SessionFormFields(Object.keys(fields));
    const disabled = sessionFormFields.some(key => !values[key.name]);
    return (
      <Fragment>
        {status && status.backendError ? (
          <div className='alert alert-error'>{translate(SessionFormErrors.genericError)}</div>
        ) : null}
        <form onSubmit={handleSubmit}>
          <fieldset>
            {sessionFormFields.map(field => {
              const { label, placeholder, ...rest } = field;
              return (
                <Field
                  key={field.name}
                  label={translate(label.key)}
                  placeholder={translate(placeholder.key)}
                  {...rest}
                />
              );
            })}
          </fieldset>
          <div className='regsiter-form-action clearfix'>
            <Button type='submit' outline={false} disabled={disabled}>
              {translate(SessionFormSubmitButton.label.key)}
            </Button>
          </div>
        </form>
      </Fragment>
    );
  }
}

SessionForm.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  values: PropTypes.object.isRequired,
  fields: PropTypes.object.isRequired,
  setStatus: PropTypes.func,
  status: PropTypes.object,
  t: PropTypes.func
};

export const FormikSessionFormCreator = (options = {}) =>
  translate('dj-webhandler')(
    withFormik({
      mapPropsToValues,
      validationSchema: () => validationSchema(),
      handleSubmit,
      displayName: 'SessionForm',
      ...options
    })(SessionForm)
  );

const FormikEnhancedSessionForm = FormikSessionFormCreator();
FormikEnhancedSessionForm.displayName = 'SessionForm';

export { FormikEnhancedSessionForm };



