import React from 'react';
import PropTypes from 'prop-types';
import { util } from '@helpers/utils';

export default class OtpField extends React.PureComponent {
  constructor() {
    super();
    this.input = null;
  }
  // Focus on first render
  // Only when shouldAutoFocus is true
  componentDidMount() {
    const {
      input,
      props: { focus, shouldAutoFocus }
    } = this;

    if (input && focus && shouldAutoFocus) {
      input.focus();
    }
  }

  componentDidUpdate(prevProps) {
    const {
      input,
      props: { focus }
    } = this;

    // Check if focusedInput changed
    // Prevent calling function if input already in focus
    if (prevProps.focus !== focus && (input && focus)) {
      input.focus();
      input.select();
    }
  }

  getClasses = (...classes) => classes.filter(c => !util.isStyleObject(c) && c !== false).join(' ');

  render() {
    const {
      separator,
      isLastChild,
      value,
      hasErrored,
      disabledStyle,
      inputStyle,
      focus,
      focusStyle,
      errorStyle,
      isDisabled,
      onChange,
      shouldAutoFocus,
      ...rest
    } = this.props;

    return (
      <div style={{ display: 'flex', alignItems: 'center' }}>
        <input
          style={Object.assign(
            { width: '1em', textAlign: 'center' },
            inputStyle,
            focus && util.isStyleObject(focusStyle) && focusStyle,
            hasErrored && util.isStyleObject(errorStyle) && errorStyle,
            isDisabled && util.isStyleObject(disabledStyle) && disabledStyle
          )}
          type='tel'
          value={value}
          maxLength='1'
          ref={input => {
            this.input = input;
          }}
          disabled={isDisabled}
          onChange={onChange}
          shouldautofocus={shouldAutoFocus.toString()}
          {...rest}
        />
        {!isLastChild && separator}
      </div>
    );
  }
}

OtpField.propTypes = {
  numInputs: PropTypes.number,
  onChange: PropTypes.func.isRequired,
  separator: PropTypes.object,
  containerStyle: PropTypes.object,
  inputStyle: PropTypes.object,
  focusStyle: PropTypes.object,
  isDisabled: PropTypes.bool,
  disabledStyle: PropTypes.object,
  hasErrored: PropTypes.bool,
  errorStyle: PropTypes.object,
  shouldAutoFocus: PropTypes.bool,
  isLastChild: PropTypes.bool,
  focus: PropTypes.bool,
  value: PropTypes.string
};



