import React from 'react';
import { translate } from 'react-i18next';
import PropTypes from 'prop-types';
import SmileSvg from '@src/assets/images/smile.svg';

import './ThankYou.css';

const ThankYou = props => {
  const { t } = props;
  const translate = t;
  return (
    <div className='container'>
      <div className='step-register-qualified-fullbox'>
        <div className='qualified-container'>
          <img src={SmileSvg} />
          <div className='register-qualified-title'>{translate('thankYouPage.title')}</div>
          <div className='register-qualified-meta'>{translate('thankYouPage.subTitle')}</div>
        </div>
      </div>
    </div>
  );
};

ThankYou.propTypes = {
  t: PropTypes.func.isRequired
};

const EnhancedThankYouPage = translate('dj-webhandler')(ThankYou);
EnhancedThankYouPage.displayName = 'ThankYou';

export default EnhancedThankYouPage;



