import React from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';

const TextField = props => {
  const {
    field: { name, value, ...field },
    form: { touched, errors },
    label,
    type,
    validation,
    className,
    hidden,
    placeholder,
    addOnText,
    ...rest
  } = props;
  const error = errors[name];
  const touch = touched[name];
  const fieldType = type === 'string' ? 'text' : type;
  const classes = cx(
    'input-field',
    {
      'error-field': !!(touch && error)
    },
    {
      'hidden-field': !!hidden
    },
    className
  );
  return (
    <div className={classes}>
      <label>
        {validation && validation.isRequired ? <span className='required'>*&nbsp;</span> : null}
        <span className='label'>{label}</span>
      </label>
      <span className='input-wrapper input-group'>
        {addOnText ? <span className='input-group-addon'>{addOnText}</span> : null}
        <input id={name} type={fieldType} placeholder={placeholder} value={value ? value : ''} {...field} {...rest} />
      </span>
      {touch && error && <span className='error'>{error}</span>}
    </div>
  );
};

TextField.propTypes = {
  field: PropTypes.shape({
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
  }),
  form: PropTypes.shape({
    errors: PropTypes.object,
    touched: PropTypes.object
  }),
  type: PropTypes.string,
  name: PropTypes.string,
  label: PropTypes.string,
  className: PropTypes.string,
  placeholder: PropTypes.string,
  validation: PropTypes.object,
  hidden: PropTypes.bool,
  addOnText: PropTypes.string,
  rest: PropTypes.object
};

TextField.defaultProps = {
  field: {
    value: ''
  },
  form: {
    errors: {},
    touched: {}
  },
  type: 'text',
  name: '',
  label: '',
  className: '',
  placeholder: ''
};

export default TextField;



